<template>
  <div class="topinfo-wrap">
    <div class="topinfo" :style="backgroundUrl">
      <router-link  tag="div"  to="/toplist" class="prev-toplist">返回排行榜</router-link>
      <div class="topinfo-text">
        <h2>{{topList.listTitle}}</h2>
        <p>{{topList.time}} 更新</p>
      </div>
    </div>
    <ul class="topinfo-list">
      <li v-for="(val,i) in topList.list" :key='i' @click='musicList({id:val.songid,singer:val.singer,name:val.name})'>
          <div class="top-list-num">{{i+1}}</div>
          <div class="top-list-info">
            <h3>{{val.name}}</h3>
            <p class="song_list__desc">{{val.singer}}</p>
          </div>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  data(){
    return {
      topList:{img:'',list:[],listTitle:'',time:''}
    }
  },
  mounted(){
    let id = this.$route.params.id
    let url = "https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg?g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&tpl=3&page=detail&type=top&topid=" + id + "&_=1492910996732";
    this.$http.jsonp(url,{ jsonp: 'jsonpCallback' }).then(d=>{
      this.topList.img=d.body.topinfo.pic_album   
      this.topList.listTitle = d.body.topinfo.ListName
      this.topList.time = d.body.update_time  
       d.body.songlist.forEach(val => {
          this.topList.list.push({
              name:val.data.songname,
              img:"https://y.gtimg.cn/music/photo_new/T002R150x150M000"+val.data.albummid+".jpg?max_age=2592000",
              songid:val.data.songid,
              singer:val.data.singer[0].name})
      });
    })
  },
  computed:{
     backgroundUrl(){
         return  "background:url("+this.topList.img+") no-repeat 0 0 /100% ;"
     }
  },
  methods:{
    ...mapActions(["musicList"])
  }
}
</script>
<style lang = 'less'>

.topinfo-wrap{
  height: 82rem;
  overflow-y: scroll;
  background-color: rgb(224, 224, 224);
}
.topinfo{
  width: 100%;
  height: 54rem;
  position: relative;
}
.prev-toplist{
  font-size: 3rem;
  line-height: 4rem;
  /*width: 100%;*/
  background-color: rgb(224, 224, 224);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  }
.topinfo .topinfo-text{
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 20%;
  padding-top: 40%;
  background-image: -webkit-linear-gradient(top,transparent,rgb(224, 224, 224) 80%);
}
.topinfo-text h2{
  font-size: 3.5rem;
  line-height: 5rem;
  height: 5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-left: 1rem;
}
.topinfo-text p{
  font-size: 2rem;
  line-height: 3rem;
  height: 3rem;
  padding-left: 1rem;
}
.topinfo-list{
  background-color: rgb(224, 224, 224);
  border-top: 1px solid rgba(0,0,0,.4);
}
.topinfo-list li{
  height: 9rem;
}
.topinfo-list .top-list-num{
  width: 7rem;
  height: 100%;
  float: left;
  text-align: center;
  line-height: 7rem;
  font-size: 3rem;
}
.topinfo-list .top-list-info{
   width: 47rem;
   height: 100%;
   float: left;
   box-sizing: border-box;
   border-bottom:  1px solid rgba(0,0,0,.4);
   padding-right: 2rem;
   overflow: hidden;
}
.topinfo-list .top-list-info h3{
  font-size: 3.5rem;
  line-height: 5rem;
  font-weight: 400;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.topinfo-list .top-list-info p{
  font-size: 2.5rem;
  line-height: 4rem;
  color: grey;
  overflow: hidden;
}
</style>



