<template>
  <div class="play-bar">
    <div class="play-l"><img src="../../assets/img/SongImg.jpg" alt=""></div>
    <div class="play-m">
      <p class="play-info">{{musicList.length?musicList[musicPlace].singer:'QQ音乐'}}  {{musicList.length?musicList[musicPlace].name:''}}</p>
      <div class="play-btns">
        <!--上一首-->
          <span class="icon-backward"  @click='prevMusic'></span>
          <!--播放、暂停-->
          <span :class=" playState?'icon-pause':'icon-play'" @click='playStateBtn' style="width:4rem"></span>
          <!--下一首-->
          <span class="icon-forward"  @click='nextMusic'></span>
          <!--播放列表-->
          <span class="icon-align-justify"  @click='playList'></span>
      </div>
    </div>
    <audio :src="src" id="play" autoplay='autoplay' ref='audios'></audio>
    <div class="play-list" v-show="isPlayList">
        <h3 class="play-list-top">歌曲列表 共{{musicList.length}}首</h3>
        <ul>
          <li v-if="musicList" v-for="val,i in musicList" :class="{color:musicPlace==i}"> 
            <p  @click='playMusic(i)'> 
                <span>{{i+1}}</span>. 
              <span>{{val.singer}}</span> -
              <span>{{val.name}}</span>
            </p>
             <i class="icon-remove-circle" @click='deleteList(i)'></i>
          </li>
        </ul>
        <div class="close" @click='close'>关闭</div>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  data(){
      return {
        isPlayList:false
      }
  },
  watch:{
  },
  mounted(){
      this.$store.dispatch('sendAudio',this.$refs.audios)
  },
  computed: mapGetters(["musicList","src","musicPlace","playState","dom"]),
  methods:{
    close(){
        this.isPlayList=false
    },
    playList(){
      this.isPlayList=true
      // https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&new_format=1&pic=500&disstid=1144416825&type=1&json=1&utf8=1&onlysong=0&nosign=1&_=1496641481404&jsonpCallback=taogeDataCallback


      // https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&new_format=1&pic=500&disstid=2043041547&type=1&json=1&utf8=1&onlysong=0&nosign=1&_=1496641778543&jsonpCallback=taogeDataCallback

      // https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg?g_tk=5381&uin=0&format=jsonp&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&new_format=1&pic=500&disstid=1723063372&type=1&json=1&utf8=1&onlysong=0&nosign=1&_=1496644976434&jsonpCallback=taogeDataCallback
    },
    ...mapActions(["deleteList","playStateBtn","prevMusic","nextMusic","playMusic","sendAudio"])
  }
}
</script>
<style>
.play-bar{
    position: fixed;
    left:0;
    bottom: 0;
    width: 100%;
    height: 14rem;
    background-color: #fff;
}
.play-bar div{
    height: 100%;
    font-size: 2.5rem;
    float: left;
}
.music-list-btn{
  height: 4rem;
  font-size: 3rem;
}
#play{
  width: 100%;
  height: 5rem;
}
.play-l{
  width: 10rem;
}
.play-l img{
  width: 8rem;
  height: 8rem;
  margin: 3rem 1rem;
}
.play-m{
  width: 40rem;
}
.play-info{
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  margin: 1rem 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.play-m .play-btns{
  width: 100%;
  height: 5rem;
  line-height: 5rem;
  font-size: 4rem;
}
.play-m .play-btns span{
  float: left;
  margin: 0 3rem;
}

.play-bar .play-list{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40rem;
  z-index: 20;
  background-color: rgba(0,0,0,.7);
  color: #fff;
}
.play-list-top{
  font: 3rem/5rem arial;
  text-align: center;
  border-bottom: 1px solid #000;
}
.play-bar .play-list li{
  font: 2.5rem/4rem arial;
  border-bottom: 1px solid #000;
  padding: 1rem;
  height: 4rem;
}
.play-list li p{
  float: left;
  width: 85%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.play-list ul{
  height: 30rem;
  overflow-y: auto;
  border-bottom: 1px solid #000;
}
.play-list li i{
  float: right;
  color: #000;
  font-size: 3rem;
  margin-right: 1rem;
}
.play-list .close{
  width: 100%;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
}
.color{
  color: #00e09e;
}
</style>




