<template>
  <ul class="commend-list " id="radioList">
      <li  v-for="(val,index) in radioList" :key='val.radioid'>
        <div class="list_media">
            <img :src="val.picUrl" :alt="val.songListDesc">
            <span class="icon_play"></span>
        </div>
        <div class="list-info">
            {{val.Ftitle}}
        </div>
    </li>
  </ul>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  props:['radioList'],
  mounted(){
  },
  methods:{
      ...mapActions([])
  },
  computed: {...mapGetters([])}
}
</script>
<style>
#radioList li{
    width: 47%;
}
#radioList .list-info{
    height: 6rem;
    line-height: 3rem;
    font-size: 2rem;
    padding: 0 0.5rem;
}
</style>



